import React from "react";
import {
    AmmActivity,
    CoinInfo,
    TradeFloat,
    WalletMap
} from "@loopring-web/component-lib/static-resource";
import { useTokenMap } from "stores/token";
import { useRouteMatch } from 'react-router';
import { AmmDetailStore, useAmmMap } from '../../../stores/Amm/AmmMap';
import { useWalletLayer2 } from '../../../stores/walletLayer2';
import { pairDetailBlock } from '../../../hooks/help/pairDetail';
import { AmmPoolSnapshot, AmmUserRewardMap, getExistedMarket, TickerData } from 'loopring-sdk';
import { makeTickView } from '../../../hooks/help/makeTickView';
import { useAmmActivityMap } from '../../../stores/Amm/AmmActivityMap';
import { deepClone } from '../../../utils/obj_tools';
import { updateWallet } from '../../../hooks/help/updateWallet';
import {
    getUserAmmTransaction, makeMyAmmMarketArray
} from '../../../hooks/help/marketTable';
import BigNumber from 'bignumber.js'
import { AmmRecordRow } from '@loopring-web/component-lib/components/tableList/ammRecordTable/Interface';
import { AmmUserReward, TokenVolumeV3, UserAmmPoolTx } from 'loopring-sdk/dist/defs/loopring_defs';
import { MyAmmLP } from '@loopring-web/component-lib/src/static-resource';
import { LoopringAPI } from '../../../stores/apis/api';
import { getAmmPoolUserRewards } from '../../../hooks/help/myAmm';
import { useSystem } from '../../../stores/system';
import { BIG10 } from '../../../defs/swap_defs';

const makeAmmDetailExtendsActivityMap = ({ammMap, coinMap, ammActivityMap, ammKey}: any) => {
    if (ammMap && coinMap) {
        let ammDetail = deepClone(ammMap[ ammKey as string ]);
        const ammActivity = ammActivityMap [ ammKey as string ];
        
        if (ammDetail && ammDetail.coinA) {
            ammDetail.myCoinA = coinMap[ ammDetail.coinA ];
            ammDetail.myCoinB = coinMap[ ammDetail.coinB ];
            ammDetail[ 'activity' ] = ammActivity ? ammActivity : {};
        }
        return ammDetail;
    }
}
type PgAmmDetail<C extends { [ key: string ]: any }> = AmmDetailStore<C> & {
    myCoinA: CoinInfo<C>,
    myCoinB: CoinInfo<C>,
    activity: AmmActivity<C> | undefined
}

export const useCoinPair = <C extends { [ key: string ]: any }>() => {
    const match: any = useRouteMatch("/liquidity/pools/coinPair/:symbol")
    const {coinMap, tokenMap, marketArray} = useTokenMap();
    const {faitPrices} = useSystem();
    const ammMapState = useAmmMap();
    const {ammActivityMap} = useAmmActivityMap()
    const {ammMap, updateAmmMap} = ammMapState;

    // const {ammMap,updateAmmMap} = useAmmMap();
    const walletLayer2State = useWalletLayer2();
    const [walletMap, setWalletMap] =  React.useState<WalletMap<C>|undefined>(undefined);
    // const [ammRecordArray, setAmmRecordArray] = React.useState<AmmRecordRow<C>[]>([]);
    const [ammMarketArray, setAmmMarketArray] = React.useState<AmmRecordRow<C>[]>([]);
    const [myAmmMarketArray, setMyAmmMarketArray] = React.useState<AmmRecordRow<C>[]>([]);
    const [ammUserRewardMap, setAmmUserRewardMap] = React.useState<AmmUserRewardMap|undefined>(undefined);
    const [snapShotData,setSnapShotData] = React.useState<{
        tickerData: TickerData|undefined
        ammpoolsbalance: AmmPoolSnapshot|undefined
    }|undefined>(undefined);

    const [myAmm, setMyAmm] = React.useState<MyAmmLP<C>>(
        {
            feeA: 0,
            feeB: 0,
            feeDollar: 0,
            feeYuan: 0,
            reward: 0,
            rewardToken: undefined as any,
            balanceA: 0,
            balanceB: 0,
            balanceYuan: 0,
            balanceDollar: 0,
        })
    // const [ammpoolsbalance, setAmmpoolsbalance] = React.useState<AmmPoolSnapshot|undefined>(undefined);
    const [coinPairInfo, setCoinPairInfo] = React.useState<PgAmmDetail<C>>({
        myCoinA: undefined,
        myCoinB: undefined,
        activity: undefined,
        totalRewards: undefined,
        amountDollar: undefined,
        amountYuan: undefined,
        totalLPToken: undefined,
        totalA: undefined,
        totalB: undefined,
        rewardToken: undefined,
        rewardValue: undefined,
        feeA: undefined,
        feeB: undefined,
        isNew: undefined,
        isActivity: undefined,
    } as unknown as PgAmmDetail<C>);
    const [tradeFloat, setTradeFloat] = React.useState<TradeFloat | undefined>(undefined);
    const [pair, setPair] = React.useState<{ coinAInfo: CoinInfo<C> | undefined, coinBInfo: CoinInfo<C> | undefined }>({
        coinAInfo: undefined,
        coinBInfo: undefined,
    });

    React.useEffect(() => {
        const coinKey = match?.params.symbol ?? undefined;
        let _tradeFloat: Partial<TradeFloat> = {}
        const [, coinA, coinB] = coinKey.match(/(\w+)-(\w+)/i)
        let {
            amm,
            market
        } = getExistedMarket(marketArray, coinA, coinB);

        const _coinPairInfo = makeAmmDetailExtendsActivityMap({ammMap, coinMap, ammActivityMap, ammKey: amm})
        setCoinPairInfo(_coinPairInfo ? _coinPairInfo : {})

        if (coinMap) {
            setPair({
                coinAInfo: coinMap[ coinA ],
                coinBInfo: coinMap[ coinB ]
            })
        }

        let _walletMap: WalletMap<C>|undefined = undefined
        if (walletLayer2State.walletLayer2) {
            _walletMap = updateWallet().walletMap;
            setWalletMap(_walletMap)
            getUserAmmTransaction(market)?.then((marketTrades: UserAmmPoolTx[])=>{
                let _myTradeArray = makeMyAmmMarketArray(market, marketTrades)
                setMyAmmMarketArray(_myTradeArray?_myTradeArray:[])
            })

        }

        if (amm && market && ammMap) {
            // let pairPromise =  usePairTitleBlock({market})
            //TODO should add it into websocket
            updateAmmMap();
            let apiList  = [
                pairDetailBlock({coinKey: market, ammKey: amm, ammMap}),
                getAmmPoolUserRewards()
            ];
            // @ts-ignore
            Promise.all([...apiList]).then(
                ([{ammpoolsbalance,tickMap},ammUserRewardMap]: any[]) => {
                    if (tokenMap) {
                        const _snapShotData =  {
                            tickerData: tickMap[ market ],
                            ammpoolsbalance: ammpoolsbalance,
                        }
                        _tradeFloat = makeTickView(tickMap[ market ]?tickMap[ market ]: {})
                        setTradeFloat(_tradeFloat as TradeFloat);
                        setCoinPairInfo({..._coinPairInfo,tradeFloat: {..._tradeFloat,
                              APY:  _coinPairInfo.APY
                            }})

                        setSnapShotData(_snapShotData)
                        setAmmUserRewardMap(ammUserRewardMap)
                        const _myAmm:MyAmmLP<C> = makeMyAmm(market,_walletMap,ammUserRewardMap,_snapShotData);
                        setMyAmm(_myAmm);
                    }
                }).catch((error)=>{
                console.log(error);
                throw  Error
            })
        }

    }, []);
    const makeMyAmm = React.useCallback((market:any,_walletMap,ammUserRewardMap,snapShotData)=>{
        const [, coinA, coinB] = market.match(/(\w+)-(\w+)/i)
        if(  _walletMap
            && ammUserRewardMap && ammUserRewardMap['AMM-'+market]
            &&  _walletMap['LP-'+ market]
            && snapShotData && snapShotData.ammpoolsbalance){
            const ammUserReward:AmmUserReward = ammUserRewardMap['AMM-'+market]

            // @ts-ignore
            const ratio = BigNumber(_walletMap['LP-' + market].detail.total).div(snapShotData.ammpoolsbalance.lp.volume)
            if (coinMap && tokenMap) {
                let _myAmm: MyAmmLP<C> = {
                    feeA: ammUserReward ? Number(ammUserReward.feeRewards[ 0 ]) : 0,
                    feeB: ammUserReward ? Number(ammUserReward.feeRewards[ 1 ]) : 0,
                    // feeDollar: 0,
                    // feeYuan: 0,
                    reward: ammUserReward ?  Number(ammUserReward.currentRewards[ 0 ].volume):0,
                    rewardToken: ammUserReward && ammUserReward.currentRewards[ 0 ] ? coinMap[ ammUserReward.currentRewards[ 0 ].tokenId ]:undefined,
                    balanceA: ratio.times(snapShotData.ammpoolsbalance.pooled[ 0 ].volume).div(BIG10.pow(tokenMap[coinA].decimals)).toNumber(),
                    balanceB: ratio.times(snapShotData.ammpoolsbalance.pooled[ 1 ].volume).div(BIG10.pow(tokenMap[coinB].decimals)).toNumber(),
                    balanceYuan: 0,
                    balanceDollar: 0,
                }  as MyAmmLP<C>
                _myAmm.feeDollar = _myAmm.feeA  * faitPrices[coinA].price;
                _myAmm.feeDollar = _myAmm.feeB  * faitPrices[coinB].price;
                return  _myAmm
            }
        }
        return {
            feeA: 0,
            feeB: 0,
            feeDollar: 0,
            feeYuan: 0,
            reward: 0,
            rewardToken: undefined as any,
            balanceA: 0,
            balanceB: 0,
            balanceYuan: 0,
            balanceDollar: 0,
        }
    },[])

    React.useEffect(() => {
        switch (walletLayer2State.status) {
            case "ERROR":
                walletLayer2State.statusUnset();
                // setState('ERROR')
                //TODO: show error at button page show error  some retry dispath again
                break;
            case "DONE":
                walletLayer2State.statusUnset();
                const {walletMap : _walletMap} = updateWallet();
                setWalletMap(_walletMap)
                if (_walletMap) {
                    let {amm, market} = getExistedMarket(marketArray, pair.coinAInfo?.simpleName as string, pair.coinBInfo?.simpleName as string);
                    const _myAmm:MyAmmLP<C> = makeMyAmm(market,_walletMap,ammUserRewardMap,snapShotData);
                    setMyAmm(_myAmm)
                    getUserAmmTransaction(market)?.then((marketTrades)=>{
                        let _myTradeArray  = makeMyAmmMarketArray(market, marketTrades)
                        setMyAmmMarketArray(_myTradeArray?_myTradeArray:[])
                    })
                }
                break;
            default:
                break;

        }
    }, [walletLayer2State.status,setMyAmmMarketArray,ammUserRewardMap,makeMyAmm,snapShotData])

    //TODO tickMap
    React.useEffect(() => {
    }, [])

    React.useEffect(() => {
        if (ammMapState.status === "ERROR") {
            //TODO: solve error
            ammMapState.statusUnset();
        } else if (ammMapState.status === "DONE") {
            ammMapState.statusUnset();
            if (ammMapState.ammMap && pair.coinAInfo?.simpleName && pair.coinBInfo?.simpleName) {
                const _coinPairInfo = makeAmmDetailExtendsActivityMap(
                    {
                        ammMap,
                        coinMap,
                        ammActivityMap,
                        ammKey: 'AMM-' + pair.coinAInfo.simpleName + pair.coinBInfo.simpleName
                    })
                setCoinPairInfo({...coinPairInfo,..._coinPairInfo})

                // setCoinPairInfo(_coinPairInfo ? _coinPairInfo : {})
            }

            // tokenState.statusUnset()
            // setState('DONE');
        }
    }, [ammMapState.status, ammMapState.ammMap, ammMapState.statusUnset, pair, ammActivityMap, tokenMap, setCoinPairInfo])



    return {
        walletMap,
        myAmm,
        // tickerData,
        coinPairInfo,
        snapShotData,
        // ammpoolsbalance,
        pair,
        tradeFloat,
        ammMarketArray,
        myAmmMarketArray,
    }
}